      return{
        menus:[
          { menuname : "#nz",menucon : "" },
          { menuname : "#ny",menucon : "" },
          { menuname : "#xb",menucon : "" },
          { menuname : "#sj",menucon : "" },
          { menuname : "#mz",menucon : "" },
          { menuname : "#sp",menucon : "" },
          { menuname : "#yd",menucon : "" },
          { menuname : "#bh",menucon : "" },
          { menuname : "#my",menucon : "" },
          { menuname : "#nz2",menucon : "" },
          { menuname : "#dq",menucon : "" },
          { menuname : "#jf",menucon : "" },
          { menuname : "#jj",menucon : "" },
          { menuname : "#dn",menucon : "" },
          { menuname : "#ht",menucon : "" },
          { menuname : "#jk",menucon : "" },
          { menuname : "#dq",menucon : "" },
          { menuname : "#jf",menucon : "" }
        ]
      }

    }
  }
</script>

<style scoped>
  @import  "../../assets/search.css";
  .menu{
    overflow-x: hidden;
    overflow-y: auto;
  }
  .menu::-webkit-scrollbar{
    display: none;
  }
</style>



